      z-index: 100;
    }

    &__bar {
      width: 126px;
      height: 15px;
      position: absolute;
      top: 195px;
      transform: translateX(-1px);
      z-index: 99;
      &--progress {
        width: 0;
        background-color: #ffbc12;
        height: 15px;
      }
    }
  }
</style>
